@import "@/sass/variables.scss";












































































































































.loginCom{
	width: 100%;
	// height: 100%;
	height: calc(100vh - #{$navHeight});
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $themeColor;
	.login_panel{
		padding: 20px;
		background-color: #fff;
		@include commonShadow;
		border-radius: 5px;
		&_title{
			text-align: center;
			font-size: 24px;
			color: #000;
			padding-bottom: 20px;
		}
		&_form{
			.el-form-item__label{
				font-size: 16px;
			}
			&_foot{
				&_link{
					width: 100%;
					display: flex;
					justify-content: flex-end;
					cursor: pointer;
					&_text{
						font-size: 14px;
					}
				}
				&_btn{
					width: 100%;
					letter-spacing: 8px;
					margin-top: 8px;
					font-weight: 700;
					font-size: 16px;
				}
			}
		}
	}
}
